.create,
.design {
  margin: 0;
}
.create {
  color: var(--color-skyblue);
}
.inspire {
  margin: 0;
  color: #f9ffb5;
}
.design-create-inspire-container {
  position: relative;
  font-size: var(--font-size-53xl);
  display: inline-block;
  text-align: center;
  width: 278px;
}
.welcome {
  align-self: stretch;
  position: relative;
  color: var(--color-darkslategray);
}
.my-projects {
  position: relative;
  line-height: 18px;
}
.button,
.text {
  display: flex;
  align-items: center;
  justify-content: center;
}
.button {
  border-radius: var(--br-31xl);
  background-color: var(--color-white);
  flex-shrink: 0;
  flex-direction: row;
  padding: var(--padding-5xl) var(--padding-29xl);
  cursor: pointer;
  color: var(--color-gray);
}
.text {
  flex-direction: column;
  padding: var(--padding-67xl) 0 0;
  gap: var(--gap-5xl);
}
.frame-child {
  position: relative;
  border-radius: var(--br-21xl);
  width: 172px;
  height: 192px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.frame-parent {
  width: 172px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.image-gallery {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: var(--padding-67xl) 0 0;
  gap: var(--gap-lg);
}
.vector-icon {
  position: relative;
  width: 53.5px;
  height: 43.5px;
  object-fit: cover;
}
.div,
.div1,
.icon-twitter {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.icon-twitter {
  flex-shrink: 0;
  align-items: flex-end;
  justify-content: flex-start;
  cursor: pointer;
}
.div,
.div1 {
  background-color: var(--color-darkslategray);
  overflow: hidden;
  justify-content: center;
  padding: var(--padding-67xl) var(--padding-45xl);
  box-sizing: border-box;
}
.div1 {
  width: 1733px;
  flex-shrink: 0;
  align-items: flex-end;
}
.div {
  position: relative;
  width: 100%;
  height: 2325px;
  align-items: flex-start;
  gap: 432px;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: #f0acb1;
  font-family: var(--font-poppins);
}
@media screen and (max-width: 420px) {
  .text {
    width: auto;
    align-self: unset;
    height: auto;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }
}
